@media only screen and (max-width: 767px) {
    /* For Tab */
    .cardEdit {
        margin-bottom: 10px;
    }

    .cardPrcing {
        width: 20rem !important;
    }

}

@media only screen and (max-width: 480px) {
    /* For Mobile */
    .cardEdit {
        margin-bottom: 10px;
    }

    #headerMainText {
        padding-bottom: 30px !important;
    }

    #headerText {
        font-size: 30px;
    }

    .cardPrcing {
        width: 20rem !important;
    }

    #footerContainer{
        padding-top: 20px;
    }

    #contactText {
        font-size: 30px;
    }

    #footerImgContainer {
        width: 80%;
    }

    .contactImg {
        width: 70%;
    }

    #bottomFooterInfo {
        font-size: 12px;
    }

    #faqContainer {
        width: 90% !important;
    }

    .subTitle {
        font-size: 30px;
    }

}